/* eslint-disable no-param-reassign */
/* eslint no-underscore-dangle: [1, { "allow": ["__env"] }] */
import React, { Component, Fragment } from 'react';
import { Row, Col, Input, Icon, Select, AutoComplete, Button } from 'antd';
import { debounce } from 'lodash';
import axios from 'axios';

import { getDSMSearchRequest } from '../../../services/api';
import AntDataTable from '../DSMViewer/DSMTables/AntDataTable';

import './DSMSearchInput.scss';

const { Option } = Select;
const { OptGroup } = AutoComplete;

class SearchComponent extends Component {
  signal = axios.CancelToken.source();

  defaultColumnDefs = {
    suppressMenu: true
  };

  state = {
    tableHeight: 500,
    serachOptions: [{ Title: 'Default', value: 'default' }],
    serachOption: 'default',
    searchResults: [],
    searchColumns: [],
    searchView: 'table',
    showResultsTable: false,
    isLoading: false,
    searchValue: '',
    autoCompleteDataSource: {
      resentSearch: [],
      suggessions: []
    },
    totalItems: 0,
    page: 1,
    pageSize: 10,
    sortingKey: '',
    sortOrder: '',
    reqTime: 0
  };

  tableWrapperRef = React.createRef();

  debouncedOnChange = debounce(() => {
    this.setState({
      page: 1,
      sortingKey: '',
      sortOrder: '',
      filters: null
    });

    this.getSearchResults();
  }, 100);

  componentWillMount() {
    window.addEventListener('resize', this.handleWindowResize);
    this.setState({
      autoCompleteDataSource: {
        resentSearch: [],
        suggessions: []
      }
    });
  }

  componentWillUnmount() {
    this.signal.cancel('Canceled');
    window.removeEventListener('resize', this.handleWindowResize);
  }

  onChange = e => {
    // const { value } = e.target;
    const { autoCompleteDataSource } = this.state;
    this.setState({
      autoCompleteDataSource: {
        resentSearch: autoCompleteDataSource.resentSearch,
        suggessions: e.length ? ['loading'] : []
      }
    });
    this.setState({ searchValue: e }, () => {
      this.debouncedOnChange();
    });
  };

  handleTableChange = (pagination, filters, sorter) => {
    console.log('handleTableChange....', pagination, filters, sorter);
    this.setState(
      {
        page: pagination.current,
        pageSize: pagination.pageSize,
        sortingKey: sorter.columnKey,
        sortOrder: sorter.order,
        filters
      },
      () => {
        this.getSearchResults();
      }
    );
  };

  getReuestTimer = reqStartTime => {
    const now = Date.now();
    const seconds = Math.floor((now - reqStartTime) / 1000);
    const milliseconds = Math.floor((now - reqStartTime) % 1000);
    this.setState({ reqTime: `${seconds}.${milliseconds} seconds` });
  };

  getSearchResults = async () => {
    const {
      searchValue,
      serachOption,
      page,
      pageSize,
      sortingKey,
      sortOrder,
      filters
    } = this.state;
    const { autoCompleteDataSource } = this.state;

    this.handleWindowResize();

    const currentSession = {};
    this.lastSession = currentSession;

    if (!searchValue) {
      this.onSearchEmpty();
      return;
    }
    this.setState({
      autoCompleteDataSource: {
        resentSearch: autoCompleteDataSource.resentSearch,
        suggessions: []
      }
    });
    this.setState({
      showResultsTable: true,
      isLoading: true
    });
    try {
      // Parse sorting string.
      let sort = '';
      if (sortOrder === 'ascend') {
        sort = `&sortby=${sortingKey}`;
      }
      if (sortOrder === 'descend') {
        sort = `&sortby=-${sortingKey}`;
      }

      // Parse Filter string
      let filterStr = [];
      if (filters) {
        filterStr = Object.keys(filters).map(key => {
          const value = filters[key] ? filters[key] : '';
          return `&contains-${key}=${value}`;
        });
      }

      const filter = filterStr.join('');
      const reqStartTime = Date.now();
      const res = await getDSMSearchRequest(
        searchValue,
        {
          page,
          pageSize,
          sorting: sortingKey ? sort : '',
          filtering: filter
        },
        {
          cancelToken: this.signal.token
        }
      );

      if (this.lastSession !== currentSession) return;
      this.getReuestTimer(reqStartTime);
      this.onGetSearchSuccess(res);
    } catch (e) {
      this.onSearchEmpty();
    }
  };

  onGetSearchSuccess = res => {
    this.parseTableData(res.data ? res.data : []);
    this.setState({
      isLoading: false,
      totalItems: res.headers['total-items'] ? res.headers['total-items'] : 1000
    });
  };

  handleFilter = (selectedKeys, confirm, dataIndex) => {
    confirm();
  };

  handleReset = clearFilters => {
    console.log(clearFilters);
    clearFilters();
  };

  getColumnFilterProps = dataIndex => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => {
      return (
        <div style={{ padding: 8 }}>
          <Input
            ref={node => {
              this.searchInput = node;
            }}
            placeholder={`Search ${dataIndex}`}
            value={selectedKeys[0]}
            onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
            onPressEnter={() => this.handleFilter(selectedKeys, confirm, dataIndex)}
            style={{ width: 188, marginBottom: 8, display: 'block' }}
          />
          <Button
            type="primary"
            onClick={() => this.handleReset(clearFilters)}
            size="small"
            style={{ width: 90 }}
          >
            Reset
          </Button>
          <Button
            type="primary"
            onClick={() => this.handleFilter(selectedKeys, confirm, dataIndex)}
            size="small"
            style={{ width: 90 }}
          >
            Apply
          </Button>
        </div>
      );
    }
  });

  createColDefs = () => {
    let { filters } = this.state;
    filters = filters || {};
    const colDefs = [
      {
        title: 'Hostname',
        dataIndex: 'name',
        key: 'name',
        className: 'name',
        sorter: true,
        filteredValue: filters.name || '',
        ...this.getColumnFilterProps('name')
      },
      {
        title: 'IP',
        dataIndex: 'ip',
        key: 'ip',
        className: 'ip',
        sorter: true,
        filteredValue: filters.ip || '',
        ...this.getColumnFilterProps('ip')
      },
      {
        title: 'Type',
        dataIndex: 'type',
        key: 'type',
        className: 'type',
        sorter: true,
        filteredValue: filters.type || null,
        filters: [
          { text: 'Router', value: 'ROUTER' },
          { text: 'Switch', value: 'SWITCH' },
          { text: 'LB', value: 'LB' }
        ]
      },
      {
        title: 'Vendor',
        dataIndex: 'vendor',
        key: 'vendor',
        className: 'vendor',
        sorter: true,
        filteredValue: filters.vendor || null,
        filters: [
          { text: 'CISCO', value: 'CISCO' },
          { text: 'Checkpoint', value: 'Checkpoint' },
          { text: 'RADWARE', value: 'RADWARE' }
        ]
      },
      {
        title: 'DN',
        dataIndex: 'dn',
        key: 'dn',
        className: 'dn',
        render: dn => {
          if (dn && dn.length) {
            return dn.join(', ');
          }
          return dn;
        }
      },
      {
        title: 'Country',
        dataIndex: 'country',
        key: 'country',
        className: 'country',
        sorter: true,
        filteredValue: filters.country || null,
        filters: [
          { text: 'Netherlands', value: 'Netherlands' },
          { text: 'Liberty Global', value: 'Liberty Global' },
          { text: 'United Kingdom', value: 'United Kingdom' }
        ]
      },
      {
        title: 'Value',
        dataIndex: 'value',
        key: 'value',
        className: 'value'
      },
      {
        title: 'Value Type',
        dataIndex: 'value_type',
        key: 'value_type',
        className: 'value_type'
      }
    ];
    return colDefs;
  };

  parseTableData = data => {
    const { meta } = data;
    // const colDefs = [];
    // data.meta.fields.forEach((col, index) => {
    //   if (col.isShow && col.name !== 'id') {
    //     colDefs.push({
    //       title: col.label,
    //       dataIndex: col.name,
    //       key: col.name,
    //       className: col.name,
    //       width: col.width ? col.width : 'auto',
    //       ...this.getColumnFilterProps(col.name)
    //     });
    //   }
    // });

    const rowData = data.data.map((d, index) => {
      return {
        ...d,
        key: index
      };
    });

    this.setState({
      searchResults: rowData,
      searchColumns: this.createColDefs(),
      searchView: meta.view,
      showResultsTable: true
    });
  };

  onSearchEmpty = () => {
    // message.error("Unfortunately there was an error getting the results");
    this.setState({
      searchResults: [],
      searchColumns: [],
      searchView: 'table',
      showResultsTable: true,
      isLoading: false,
      page: 0,
      totalItems: 0
    });
  };

  setTableDimensions = () => {
    const marginBottom = 70;
    const offsetTop = this.tableWrapperRef.current.getBoundingClientRect().top;
    const tableHeight = window.innerHeight - offsetTop - marginBottom - 50;
    this.setState({ tableHeight });
  };

  handleWindowResize = () => {
    this.setTableDimensions();
  };

  searchOptionChange = serachOption => {
    this.setState({ serachOption });
  };

  renderOption = () => {
    const { autoCompleteDataSource } = this.state;
    const options = Object.keys(autoCompleteDataSource).map(group => {
      return (
        <OptGroup key={group} label={group === 'suggessions' ? 'Suggestions' : 'Recent Searches'}>
          {autoCompleteDataSource[group].map(item => (
            <Option key={item} text={item}>
              {item === 'loading' ? (
                <Icon type="loading" />
              ) : (
                <div className="global-search-item">
                  <span className="global-search-item-desc">{item}</span>
                  {group === 'resentSearch' ? (
                    <span className="search-history-remove">Remove</span>
                  ) : (
                    ''
                  )}
                </div>
              )}
            </Option>
          ))}
        </OptGroup>
      );
    });

    return options;
  };

  render() {
    const {
      searchValue,
      isLoading,
      reqTime,
      serachOptions,
      serachOption,
      searchResults,
      searchColumns,
      tableHeight,
      searchView,
      showResultsTable,
      totalItems,
      page
    } = this.state;

    const colDefs = this.createColDefs();
    return (
      <Fragment>
        <div className="search-input-wrapper initial-search-done">
          <Row gutter={12}>
            <Col span={6}>
              <Select
                placeholder="Select Option"
                className="search-select-options"
                onSelect={this.searchOptionChange}
                value={serachOption}
              >
                {serachOptions.map(option => (
                  <Option value={option.value} key={option.value}>
                    {option.Title}
                  </Option>
                ))}
              </Select>
            </Col>
            <Col span={18}>
              <div className="global-search-wrapper">
                <AutoComplete
                  className="search-input"
                  size="large"
                  style={{ width: '100%' }}
                  dropdownStyle={{ width: '500px' }}
                  onSearch={this.handleSearch}
                  optionLabelProp="text"
                  onChange={e => this.onChange(e)}
                  ref={node => {
                    this.searchInput = node;
                  }}
                  autoFocus
                >
                  <Input
                    className="search-input"
                    placeholder="Search..."
                    prefix={<Icon type="search" style={{ color: 'rgba(0,0,0,.25)' }} />}
                    size="large"
                  />
                </AutoComplete>
                {totalItems && searchValue && !isLoading && (
                  <div className="search-info">
                    About {parseInt(totalItems).toLocaleString()} results ({reqTime})
                  </div>
                )}
              </div>
            </Col>
          </Row>
        </div>

        <div className="search-table-wrapper">
          <div ref={this.tableWrapperRef} className="search-results">
            {searchView === 'table' && showResultsTable && (
              <AntDataTable
                colDefs={colDefs}
                rowData={searchResults}
                tableHeight={tableHeight}
                totalItems={totalItems}
                page={page}
                isLoading={isLoading}
                onTableChange={this.handleTableChange}
              />
            )}
          </div>
        </div>
      </Fragment>
    );
  }
}

export default SearchComponent;
